* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
    scroll-snap-type: y mandatory;
}

.app {
    height: 100vh;
    background-color: black;
    display: grid;
    place-items: center;
}

.app_videos {
    position: relative;
    height: 800px;
    border-radius: 20px;
    width: 80%;
    max-width: 500px;
    overflow: scroll;
    scroll-snap-type: y mandatory;
}

/* Hide Scrollbar Chrome, Safari, Opera*/
.app_videos::-webkit-scrollbar {
    display: none;
}

/* Hide Scrollbar IE, Edge, Firefox */
.app_videos {
    /* IE and Edge */
    -ms-overflow-style: none;
    /* Firefox */
    scrollbar-width: none;
}
.video {
    position: relative;
    background-color: white;
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
}

.video_player {
    object-fit: fill;
    width: 100%;
    height: 100%;
}
.videoFooter {
    position: relative;
    color: white;
    bottom: 150px;
    margin-left: 40px;
    display: flex;
}

.videoFooter_text {
    flex: 1 1;
}

.videoFooter_text>p {
    padding-bottom: 20px;
}

.videoFooter_text>h3 {
    padding-bottom: 20px;
}

.videoFooter_icon {
    position: absolute;
}

.videoFooter_ticker>.ticker {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-left: 30px;
    width: 60%;
}

.videoFooter_ticker h1 {
    padding-top: 7px;
}

.videoFooter_record {
    -webkit-animation: spinTheRecord infinite 5s linear;
            animation: spinTheRecord infinite 5s linear;
    height: 50px;
    -webkit-filter: invert(1);
            filter: invert(1);
    position: absolute;
    bottom: 0;
    right: 20px;
}

@-webkit-keyframes spinTheRecord {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spinTheRecord {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.videoSidebar {
    position: absolute;
    top: 50%;
    right: 10px;
    color: white;
}

.videoSidebar_button {
    padding: 20px;
    text-align: center;
}
